import React, { Component } from 'react';
import FormGenerator from './FormGenerator';
import { MAKESITEENTRY, UPDATESITEENTRY } from '../../graphql/mutation/remote';
import { SITEENTRY } from '../../graphql/query/remote';
import InputAdornment from '@material-ui/core/InputAdornment';
import { Query } from 'react-apollo';

class CreateSiteEntry extends Component {    
    formatDate(date) {
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();
    
        if (month.length < 2) month = '0' + month;
        if (day.length < 2) day = '0' + day;
    
        return [year, month, day].join('-');
    }
    mergeData(defaults, data) {
        let result = {};
        Object.keys(defaults).forEach(e => {
            // console.log(e);
            result[e] = {...defaults[e]};
            if (typeof (defaults[e].default) === "string" || typeof (defaults[e].default) === "number") {
                result[e].default = data[e];
            }

            else if (defaults[e].type === Boolean) {                
                result[e].default = data[e];
            }

            else if (defaults[e].type === "date") {
                result[e].default = this.formatDate(data[e]);
            }

            else if (defaults[e].default instanceof Object) {
                result[e].default = {...this.mergeData(defaults[e].default, data[e])};
            }
            else {
                throw new Error("merge error");
            }
        });
        return result;
    }
    render() {
        const { siteId, edit = false, id } = this.props.match.params;
        const repeater = {
            quantity: {
                type: "number",
                default: ""
            },
            cost: {
                type: "number",
                default: "",
                startAdornment: <InputAdornment position="start">₹</InputAdornment>
            },
            paid: {
                type: Boolean,
                default: false
            }
        };        
        const defaults = {
            mistri: {
                default: repeater
            },
            labour: {
                default: repeater
            },
            eit: {
                default: repeater
            },
            morang: {
                default: repeater
            },
            githi: {
                default: repeater
            },
            cement: {
                default: repeater
            },
            saria: {
                default: repeater
            },
            dust: {
                default: repeater
            },
            other: {
                default: {
                    quantity: {
                        type: "text",
                        default: "",
                    },
                    cost: {
                        type: "number",
                        default: ""
                    },
                    paid: {
                        type: Boolean,
                        default: false
                    }
                }
            },
            createdAt: {
                type: "date",
                default: new Date()

            }
        };

        if (edit) {
            return (
                <Query query={SITEENTRY} variables={{ id }}>
                    {({ loading, error, data }) => {
                        if (loading) {
                            return (<span>loading...</span>);
                        }
                        else if (error) {
                            return (<span>Error Occured!</span>);
                        }
                        console.log("old Default...", defaults);
                        let newDefaults = this.mergeData(defaults, data.siteEntry);
                        console.log("new Default...", newDefaults);
                        return (
                            <FormGenerator {...this.props} defaults={newDefaults} submitText="Create Entry" mutation={UPDATESITEENTRY} mutateFunc={(fn, data) => { return fn({ variables: { id, siteId, data } }); }} title="Create a new entry"></FormGenerator>
                        );
                    }}
                </Query>
            );
        }
        return (
            <FormGenerator {...this.props} defaults={defaults} submitText="Create Entry" mutation={MAKESITEENTRY} mutateFunc={(fn, data) => { return fn({ variables: { siteId, data } }); }} title="Create a new entry"></FormGenerator>
        )
    }
};

export default CreateSiteEntry;



